.profile-system {
    padding-top: 24px;
    background: #EEF9FF;
    color: #31455B;
    border-radius: 0 0 8px 8px;
    padding-bottom: 37px;

    &__wrap {
        display: flex;
        justify-content: space-between;
        border: 1px solid #D2DEE6;
        border-radius: 8px;
        margin: 0 24px;

    }

    &__text {
        margin: 24px 0 18px 24px;
    }

    &__wrapp {
        display: flex;
        justify-content: space-between;
        // border: 1px solid #D2DEE6;
        border-radius: 8px;
        margin: 0 24px;
        margin-top: 35px;
        max-width: 1150px;
        align-items: center;
        // padding-bottom: 35px;
    }

    progress {
        width: 120px;
        border-radius: 10px;
        -webkit-appearance: none;
        border: none;
        overflow: hidden;
        height: 4px;
        // background: #31455B;
    }

    progress::-moz-progress-bar {
        background: #31455B;

    }

    progress::-webkit-progress-value {
        background: #31455B;

    }

    &__progress p {
        font-family: Montserrat;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 28px;
        color: #31455B;
    }

    &__progress {
        margin-left: 22px;
    }

    &__group {
        max-width: 448px;
        width: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        // margin-bottom: 32px;
        flex-wrap: wrap;

        &-items {
            width: 100%;
            display: flex;
            justify-content: end;
            margin-top: 32px;
        }
    }



    &__link {
        width: 288px;
        height: 56px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-right: 1px solid #D2DEE6;
        position: relative;



        & input#r1:checked~::before {
            content: "";
            position: absolute;
            top: -1px;
            right: -1px;
            bottom: -1px;
            left: -1px;
            background: #33B6FF;
            border-radius: 8px 0 0 8px;
            z-index: -1;
        }

        & input#r2:checked~::before {
            content: "";
            position: absolute;
            top: -1px;
            right: -1px;
            bottom: -1px;
            left: -1px;
            background: #33B6FF;
            // border-radius: 8px 0 0 8px;
            z-index: -1;
        }

        & input#r3:checked~::before {
            content: "";
            position: absolute;
            top: -1px;
            right: -1px;
            bottom: -1px;
            left: -1px;
            background: #33B6FF;
            // border-radius: 8px 0 0 8px;
            z-index: -1;
        }

        & input#r4:checked~::before {
            content: "";
            position: absolute;
            top: -1px;
            right: -1px;
            bottom: -1px;
            left: -1px;
            background: #33B6FF;
            border-radius: 0 8px 8px 0;
            z-index: -1;
        }

        & input:checked~span {
            // background: #33B6FF;
            z-index: 1;
            color: #FFFFFF;
            font-family: Montserrat;
            font-style: normal;
            font-weight: 800;
            font-size: 16px;
            line-height: 40px;

        }

        // &:first-child {
        //     background: #33B6FF;
        //     border-radius:   8px 0 0 8px;
        //     color: #ffffff;
        //     border-right: 1px solid #33B6FF;
        // }

        &:last-child {
            border-right: 0;
        }
    }

    &__radio {

        font-family: Open Sans;
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 28px;
        position: relative;
        display: flex;
        align-items: center;
        color: #31455B;

        & span::before {
            content: "";
            height: 24px;
            width: 24px;
            border: 2px solid #D2DEE6;
            border-radius: 50%;
            margin-right: 16px;
            display: inline-flex;
            position: relative;
            top: 3px;
        }

        & input:checked~::before {
            border: 2px solid #33B6FF;
        }

        & input:checked~::after {
            content: "";
            width: 10px;
            height: 10px;
            background: #33B6FF;
            // border: 2px solid red;
            position: absolute;
            border-radius: 50%;
            left: 12px;
            top: 50%;
            transform: translate(-50%, -50%);
        }
    }

    &__btn1 {
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: Montserrat;
        font-style: normal;
        font-weight: normal;
        font-size: 10px;
        line-height: 18px;
        max-width: 214px;
        width: 100%;
        height: 21px;
        background: #C6EBFF;
        border-radius: 44px;
        margin-left: 18px;
    }

    &__btn2 {
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: Montserrat;
        font-style: normal;
        font-weight: normal;
        font-size: 10px;
        line-height: 18px;
        max-width: 229px;
        width: 100%;
        height: 21px;
        background: #DCE1FE;
        border-radius: 44px;
        margin-left: 18px;
    }

}

